.form {
  min-height: 80vh;
  display: grid;
}

.form .holder {
  background-color: var(--primary);
  border: 1px solid var(--accent);
  place-self: center;
  width: fit-content;
  padding: 30px;
}

.form input:is([type="text"], [type="password"]) {
  color: #fff;
  background-color: var(--secondary);
  border: 1px solid var(--accent);
  width: 20ch;
  margin: 10px;
  font-size: 20px;
}

@media (width <= 800px) {
  .form input:is([type="text"], [type="password"]) {
    width: 15ch;
  }
}

.form button {
  width: 100px;
  height: 30px;
}

.form .edit {
  text-align: right;
}

.form .submit {
  text-align: center;
  margin-top: 20px;
}

.inactive {
  display: none;
}

.error {
  color: #ff3434;
  font-weight: bold;
  transition: scale .2s;
  scale: 100%;
}

.error:empty {
  scale: 0%;
}

.selected {
  border-width: 4px;
}

#passwordHolder {
  display: inline;
  position: relative;
}

#passwordToggle {
  background-color: #0000;
  border: none;
  width: 5ch;
  height: 20px;
  margin: 0 10px 0 0;
  position: absolute;
  top: 0;
  right: 0;
}
